
@import "@leuffen/jodastyle/style_embed";
@import "src/default/loader";

body > * {
  display: none;
}
body.loaded > * {
  display: block;
}

body {
  min-height: 3000vh; // 3000vh is enough to make sure the page scrolls to correct position on reload
  opacity: 0;
  transition: opacity 0.25s;
}

body.loaded {
  min-height: unset;
  opacity: 1;
}
